import React, { useState } from "react";
import pg from "./paginator.module.css";

function Paginator(props) {
  let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
  let pageMas = [];
  for (var i = 1; i <= pagesCount; i++) {
    pageMas.push(i);
  }

  let portionCount = Math.ceil(pagesCount / props.portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPageNumber = (portionNumber - 1) * props.portionSize + 1;
  let rightPageNumber = portionNumber * props.portionSize;

  return (
    <div className={pg.pagin}>
      {portionNumber > 1 ? (
        <button
          value="back"
          onClick={() => setPortionNumber(portionNumber - 1)}
        >
          <i className="far fa-arrow-alt-circle-left"></i>
        </button>
      ) : null}

      <span className={pg.numbers}>
        {pageMas
          .filter((p) => p >= leftPageNumber && p <= rightPageNumber)
          .map((p) => {
            return (
              <span
                onClick={() => {
                  props.onPageChanged(p);
                }}
                className={props.currentPage === p && pg.active}
              >
                {p}
              </span>
            );
          })}
      </span>

      {portionNumber < portionCount ? (
        <button onClick={() => setPortionNumber(portionNumber + 1)}>
          <i className="far fa-arrow-alt-circle-right"></i>
        </button>
      ) : null}
    </div>
  );
}

export default Paginator;
