import React from "react";
import fr from "./friends.module.css";
import userPhoto from "../../assets/img/ava.png";
import { NavLink } from "react-router-dom";
import { usersAPI } from "../../api/api.js";
import Preloader from "../common/preloader/Preloader.js";
import Paginator from "../common/paginator/Paginator.js";

function Friends_item(props) {
  return (
    <div className={fr.friends_item}>
      <NavLink to={"/profile/" + props.id}>
        <img src={props.photo} className={fr.ava} />
      </NavLink>
      {props.name}
      <div className={fr.status}>"{props.status}"</div>
      <div className={fr.location}>{props.location}</div>
    </div>
  );
}

class Friends extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      friendsData: [],
      pageSize: 5,
      totalUsersCount: 0,
      currentPage: 1,
      isFetching: false,
    };
  }

  componentDidMount() {
    this.setState({ isFetching: true });
    usersAPI
      .getUsers(this.state.currentPage, this.state.pageSize, true)
      .then((response) => {
        this.setState({
          friendsData: response.items,
          totalUsersCount: response.totalCount,
          isFetching: false,
        });
        console.log(this.state);
      });
  }

  render() {
    let friendElements = this.state.friendsData.map((f) => {
      return (
        <Friends_item
          name={f.name}
          key={f.id}
          id={f.id}
          status={f.status}
          location={f.location}
          photo={f.photos.small != null ? f.photos.small : userPhoto}
          followed={f.followed}
          if={f.id}
        />
      );
    });

    return (
      <div className={fr.friends}>
        {this.state.isFetching ? null : friendElements}
        {this.state.friendsData.length > 0 ? (
          <Paginator
            totalUsersCount={this.state.totalUsersCount}
            onPageChanged={this.state.onPageChanged}
            currentPage={this.state.currentPage}
            portionSize={3}
            pageSize={this.state.pageSize}
          />
        ) : null}
        <Preloader isFetching={this.state.isFetching} />
      </div>
    );
  }
}

export default Friends;
